@use "../../assets/styles/mixins" as mixin;

.header {
    position: relative;
    padding: 10px 20px;
    height: 58px;
    background-color: white;
    box-shadow: var(--box-shadow-nav);
    color : var(--primary) ;
   img{
    width: 150px;
    height: 60px;
   }
}

.headerList{
    @include mixin.xs{
        display: none;
    }
}
.headerXs{
    display: none;
    cursor: pointer;
    transition: opacity 0.2s;
    &:hover{
        opacity: 0.8;
    }
    @include mixin.xs{
        display: block;
    }
}